*,*::before,*::after{padding:0;margin:0;box-sizing:border-box}html{font-size:62.5%}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#EDE9FE}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.users-container{width:70%;min-width:90%;margin:auto;display:flex;justify-content:center;flex-direction:column;margin-top:20px}.search-input,select{max-width:100%;width:520px;margin:auto;padding:15px 30px;border:3px solid #3e206d;border-radius:5px;color:#4B5563;font-weight:600;outline:none;font-size:1.5rem;margin-bottom:20px}.search-input:focus,select:focus{border:3px solid #3e206d}.search-input::-webkit-input-placeholder, select::-webkit-input-placeholder{color:#4B5563}.search-input:-ms-input-placeholder, select:-ms-input-placeholder{color:#4B5563}.search-input::placeholder,select::placeholder{color:#4B5563}.select-divs{display:flex;flex-direction:column;max-width:100%;width:520px;margin:auto}select{padding:10px 20px;width:240px;margin:10px 0px;margin-bottom:20px}select:nth-child(4){font-size:1.3rem;width:65px;padding:7px}.sort-title{color:#B45309}.user-table{max-width:95%;width:700px;margin:auto;margin-top:20px}table,th,td{border:3px solid #3e206d;border-collapse:collapse}thead tr{background:#6D28D9;color:#fff}tbody tr{background:#fff;font-weight:500;transition:0.3s}tbody tr .user-name-link{text-decoration:none;color:black}tbody tr:hover{background-color:#EDE9FE}tbody td:nth-child(1){font-weight:600}th,td{padding:10px 40px;font-size:1.7rem}@media screen and (max-width: 610px){.search-input,select{border:2px solid #3e206d;padding:8px 15px;font-size:1.3rem}.sort-title{font-size:1.5rem}select{padding:5px 3px;width:100px;margin-left:0px}table,th,td{border:2px solid #3e206d}th,td{padding:5px 2px;font-size:1.1rem}}

.pagination-container{width:500px;max-width:90%;margin:auto;margin-top:20px}.pagination{display:flex;list-style:none;font-size:2rem;justify-content:center;margin-bottom:30px}.page-item{border:3px solid #3e206d;margin:0px 5px;padding:5px;border-radius:5px;cursor:pointer}.page-item .page-link{text-decoration:none;font-weight:600;padding:5px 15px}.page-item .page-link:focus{color:#3e206d}@media screen and (max-width: 480px){.pagination{font-size:1.3rem}.page-item{border:2px solid #3e206d;padding:5px;border-radius:5px;cursor:pointer}.page-item .page-link{text-decoration:none;font-weight:600;padding:3px 9px}}

.loading-container{width:100%;height:100vh;display:grid;place-items:center}.loader{border:5px solid #fff;border-top:5px solid #3e206d;border-radius:50%;width:60px;height:60px;animation:spin 2s linear infinite;margin-bottom:100px}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.post-container{background-color:#fff;width:90%;max-width:700px;margin:auto;padding:25px 30px;margin-bottom:30px;margin-top:30px;border-radius:5px;position:relative;border:3px solid #3e206d}.post-title{font-size:2.3rem;color:#B45309;text-transform:uppercase;width:80%;font-family:'Open Sans', sans-serif}.post-body{font-size:1.6rem;padding:20px 0px;font-weight:500}.link-container{margin-top:20px;display:flex;align-items:center;height:30px}.post-details-link{text-decoration:none;background-color:#7C3AED;color:#fff;font-size:1.5rem;padding:8px 35px;border-radius:3px;transition:0.3s;margin-bottom:10px;font-weight:600;border:3px solid #3e206d}.post-details-link:hover{background-color:#4C1D95}.edit-delete-container{position:absolute;top:0;right:0;transform:translate(-30%, 60%);font-size:1.3rem}.edit-delete-container .post-edit-link{padding:10px 10px;border-radius:50%;transition:0.3s;color:#4B5563}.edit-delete-container .post-edit-link:hover{background:#DDD6FE}.edit-delete-container .btn-delete-btn{background:none;border:none;font-size:1.3rem;transition:0.3s;cursor:pointer;padding:10px 10px;border-radius:50%}.edit-delete-container .btn-delete-btn:hover{background-color:#DDD6FE}.comment-heading{width:700px;max-width:90%;margin:30px auto;color:#B45309;padding-left:30px;position:relative}.comment-heading::after{position:absolute;content:"";width:105px;height:3px;background:#3e206d;bottom:-10px;left:30px}@media screen and (max-width: 480px){.post-container{padding:20px 20px;border:2px solid #3e206d}.post-title{font-size:1.6rem}.post-body{font-size:1.3rem}.post-details-link{font-size:1.3rem;padding:7px 20px}.post-details-link:hover{background-color:none}.edit-delete-container{transform:translate(10%, 60%);font-size:1.2rem}.edit-delete-container .post-edit-link{padding:0px 3px}.edit-delete-container .post-edit-link:hover{background:none}.edit-delete-container .btn-delete-btn{margin-left:0px;margin-right:10px;font-size:1.2rem}.edit-delete-container .btn-delete-btn:hover{background:none}}

.loading-container{width:100%;height:100vh;display:grid;place-items:center}.loader{border:5px solid #fff;border-top:5px solid #3e206d;border-radius:50%;width:60px;height:60px;animation:spin 2s linear infinite;margin-bottom:100px}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.container{width:80%;margin:auto;background:#EDE9FE}.loadmore-btn-container{display:flex;justify-content:center;margin-bottom:30px}.btn-loadMore{padding:10px 50px;background-color:#7C3AED;border:none;color:#fff;font-size:1.6rem;font-weight:600;border-radius:4px;cursor:pointer;border:3px solid #3e206d}@media screen and (max-width: 480px){.container{width:100%}.btn-loadMore{padding:7px 30px;font-size:1.3rem}}

.comment-container{width:700px;max-width:90%;margin:auto;background:#fff;margin-bottom:10px;padding:13px 30px;border:3px solid #3e206d;border-radius:5px}.user-email{padding-bottom:10px;font-size:1.5rem;font-weight:700;color:#B45309}.user-comment{color:#4B5563;font-weight:500;font-size:1.3rem;padding-bottom:4px}

.nav-container{display:flex;justify-content:center;background-color:#3e206d;font-size:50px;border-bottom:4px solid black}.nav-link{padding:13px 40px;color:#fff;text-decoration:none;font-size:2.5rem;font-weight:600}.nav-link:hover{background:#5B21B6}.nav-link-active{border-bottom:5px solid white}.toolpit{font-size:1.5rem;padding:10px}@media screen and (max-width: 480px){.nav-link{padding:10px 20px;font-size:1.8rem}}

.profile-container{background:#6D28D9;min-height:300px;color:#FCD34D;text-align:center;border-bottom:5px solid #3e206d}.upper{margin:auto;width:auto;display:grid;place-items:center}.upper .profile-name{font-size:5rem;padding:50px;font-weight:600}.lower{width:300px;margin:auto;display:flex;justify-content:center;flex-direction:column}.profile-email,.profile-phone,.profile-website{text-align:left;font-size:2rem;margin-left:20px}.profile-email .span-text,.profile-phone .span-text,.profile-website .span-text{padding-left:10px}@media screen and (max-width: 480px){.upper .profile-name{font-size:4rem;padding:40px}.profile-email,.profile-phone,.profile-website{font-size:1.8rem;margin-left:30px}.profile-email .span-text,.profile-phone .span-text,.profile-website .span-text{padding-left:10px}}

.add-post-container{width:700px;max-width:90%;margin:auto;display:flex;flex-direction:column;margin-top:50px;height:76vh}.title-textarea{font-size:1.6rem;padding:10px 20px;border-radius:5px;font-family:'Open Sans', sans-serif;font-weight:500;color:#B45309;border:3px solid #3e206d;outline:none;margin-bottom:20px}.title-textarea:focus{border:3px solid #3e206d}.title-textarea::-webkit-input-placeholder{color:#B45309}.title-textarea:-ms-input-placeholder{color:#B45309}.title-textarea::placeholder{color:#B45309}.body-textarea{font-size:1.5rem;font-weight:500;padding:10px 20px;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;height:200px;border:3px solid #3e206d;border-radius:5px;margin-bottom:30px;outline:auto}.body-textarea:focus{border:3px solid #3e206d}.body-textarea::-webkit-input-placeholder{color:#4B5563;font-weight:500}.body-textarea:-ms-input-placeholder{color:#4B5563;font-weight:500}.body-textarea::placeholder{color:#4B5563;font-weight:500}.btn-sumbit-post{text-decoration:none;background-color:#7C3AED;color:#fff;font-size:1.5rem;padding:12px 35px;border-radius:5px;transition:0.3s;margin-bottom:10px;font-weight:600;border:3px solid #3e206d}.btn-sumbit-post:hover{background-color:#4C1D95}@media screen and (max-width: 480px){.title-textarea,.body-textarea{font-size:1.3rem;padding:7px 15px;border:2px solid #3e206d}.title-textarea:focus{border:2px solid #3e206d}.body-textarea:focus{border:2px solid #3e206d}}

.footer{text-align:center;width:100%;padding:20px;background-color:#B45309;color:#fff;font-size:2rem;font-weight:600}

